                <p>b. W przypadkach, w których podstawą przetwarzania Danych Osobowych i Danych Statystycznych jest Zgoda Użytkownika, Użytkownik ma prawo do jej wycofania w dowolnym momencie. Aby wycofać Zgodę, można wysłać wiadomość e-mail na adres: sales@np.shopping. W przypadku wycofania Zgody, Platforma ma prawo zaprzestać świadczenia Usług i zakończyć wszelkie relacje z Użytkownikiem. W przypadku wycofania Zgody Dane Osobowe i Dane Statystyczne zostaną trwale usunięte.<br/><br/>c. Platforma zobowiązuje się do zaprzestania przetwarzania Danych Osobowych i Statystycznych w ciągu piętnastu <strong>(15) dni roboczych</strong> od daty otrzymania wycofania Zgody.</p>
                <p><strong>H. PRAWA UŻYTKOWNIKA</strong><strong></strong></p>
                <p>a. Warunki niniejszej klauzuli mają zastosowanie do mieszkańców Unii Europejskiej i Europejskiego Obszaru Gospodarczego. Zgodnie z warunkami niniejszej klauzuli Użytkownikowi przysługują następujące prawa:</p>
