                <p>c. Nie będziemy w stanie odpowiedzieć na żądanie Użytkownika ani przekazać mu Danych Osobowych i Statystycznych, jeśli nie będziemy w stanie zweryfikować jego tożsamości i potwierdzić, że Dane Osobowe i Statystyczne należą do Użytkownika. W przypadku otrzymania żądania zawierającego niedokładne informacje i/lub w przypadku niemożności potwierdzenia tożsamości Użytkownika, Platforma ma prawo nie przetwarzać otrzymanego żądania i skontaktować się z Użytkownikiem w celu uzyskania wyjaśnień. W przypadku otrzymania odpowiedzi na wniosek o wyjaśnienie, Użytkownik powinien dostarczyć nowy poprawiony wniosek lub złożyć nowy wniosek.<br/><br/>d. Platforma odpowie na wniosek lub spełni warunki określone we wniosku w ciągu piętnastu <strong>(15) dni roboczych</strong> od daty jego otrzymania.</p>
